import React from 'react'
import './StudentCard.css'
import FacebookIcon from '@mui/icons-material/Facebook';
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import { useStateValue } from "../StateProvider";
import {Link} from "react-router-dom";
import { auth, db} from '../firebase';

function StudentCard({image,name,para,address,email,github,graduation,linkedln,phone,university}) {

	const [{current}, dispatch] = useStateValue()

	const addToCurrent=()=>{
		// console.log("email",email)
		dispatch({
			type: 'ADD_TO_CURRENT',
			item:{
					image: image,
					name: name,
					para: para,
					address: address,
					email: email,
					github: github,
					graduation: graduation,
					linkedln: linkedln,
					phone: phone,
					university: university,
			}
		})
		// console.log("currr",current)
	}



	return (
		<Link to="/students_profile">
			<div className="student_card" onClick = {addToCurrent}>
				<div className="image">
					<img src={image} />
				</div>
				<div className="describe">
					<h1>{name}</h1>
					<p>{para}</p>
				</div>
				<div className="icons">
					<GitHubIcon />
					<FacebookIcon />
					<LinkedInIcon />
				</div>
			</div>
		</Link>	
	)
}

export default StudentCard